.p {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser {
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    position: sticky;
    z-index: 2;
}

.p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.p-img {
    width: 100%;
    transition: all 7s ease;
}

.p:hover .p-img {
    transform: translateY(calc(-100% + 40vh));
}

@media screen and (max-width: 500px) {
    .p {
        width: 40%;
        height: 20vh;
    }

    .p-img {
        animation: move 15s ease-in-out infinite alternate;
    }

    @keyframes move {
        0% {
            transform: translateY(calc(-100% + 20vh));
        }
    }
}